import React, { Component } from 'react';
import { Modal } from 'react-bootstrap';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import { GET_NETWORK_ICON } from '../../../../Services/Utils/utils';

class AddSocialAccountModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }

    handleClose = () => {
        this.props.addAccountCallback(false)
    };

    render() {
        const { modalToggle, networkList } = this.props

        return <Modal show={modalToggle} onHide={this.handleClose}
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title>Add Social Network</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="connection_modal mx-xl-4 px-2 px-lg-4 mx-0 overflow-auto">
                    {
                        networkList.map((netwrkItem) => {
                            return <div className="d-flex justify-content-between align-items-center py-3 border-bottom">
                                <div className="d-flex align-items-center flex-grow-1 me-2 w-75">
                                    <span className="h-34px w-34px me-2 d-flex flex-shrink-0">
                                        <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(netwrkItem.id)}.svg`)} height={43} width={43} alt={GET_NETWORK_ICON(netwrkItem.id)} />
                                    </span>
                                    <div className="overflow-hidden">
                                        <span className="text-gray-800 mb-0 w-100 fw-bold text-ellipsis me-2">{netwrkItem.name}</span>
                                        {/* <span className="badge badge-light-info">Max 2 account</span> */}
                                    </div>
                                </div>
                                <div className="d-flex flex-column align-items-end flex-srink-0">
                                    <button type="button" className="btn btn-primary btn-sm btn-min-w">Connect</button>
                                </div>
                            </div>
                        })
                    }


                </div>
            </Modal.Body>
            {/* <Modal.Footer>
                        <button type="button" className="btn btn-primary btn-sm" onClick={this.handleClose}>Add Account</button>
                    </Modal.Footer> */}
        </Modal>

    }
}

export default AddSocialAccountModal;